import { Action, createReducer, on } from '@ngrx/store';
import {IUser} from "../models/IUser";
import * as userActions from '../actions/user.actions';

export const userFeatureKey = 'user';

export interface State {
  loading:boolean,
  token:string,
  errorMessage:string,
  isSubmitted:boolean,
  isAuthenticated:boolean,
  user:IUser
}

export const initialState: State = {
  loading:false,
  token:null,
  errorMessage:null,
  user:null,
  isSubmitted:false,
  isAuthenticated:false,
};

export const reducer = createReducer(
  initialState,
  on(userActions.registerUser, (state , {user}) => {
    return {
      ...state,
      loading : true
    };
  }),
  on(userActions.registerUserSuccess, (state , {result}) => {
    return {
      ...state,
      loading : false,
      isSubmitted : true
    }
  }),
  on(userActions.registerUserFailure, (state , {error}) => {
    return {
      ...state,
      loading : false,
      errorMessage : error
    }
  }),
  // Login a User
  on(userActions.loginUser, (state , {user}) => {
    return {
      ...state,
      loading : true
    };
  }),
  on(userActions.loginUserSuccess, (state , {result ,token}) => {
    localStorage.setItem('token', token);
    return {
      ...state,
      loading : false,
      isSubmitted : true,
      isAuthenticated : true,
      token : token
    }
  }),
  on(userActions.loginUserFailure, (state , {error}) => {
    localStorage.removeItem('token');
    return {
      ...state,
      loading : false,
      errorMessage : error
    }
  }),
  // Logout User
  on(userActions.logOut, (state) => {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    return {
      ...state,
      isAuthenticated : false,
      token : null,
      user : null
    }
  }),
  // get user information from server, after login
  on(userActions.loadUser, (state) => {
    return {
      ...state,
      loading : true
    }
  }),
  on(userActions.loadUserSuccess, (state, {user}) => {
    localStorage.setItem('user', JSON.stringify(user));
    return {
      ...state,
      loading : false,
      user : user
    }
  }),
  on(userActions.loadUserFailure, (state, {error}) => {
    localStorage.removeItem('user');
    return {
      ...state,
      loading : false,
      user : null
    }
  }),
  // Update Address
  on(userActions.updateAddress, (state, {address}) => {
    return {
      ...state,
      loading : true
    }
  }),
  on(userActions.updateAddressSuccess, (state, {user}) => {
    localStorage.setItem('user', JSON.stringify(user));
    return {
      ...state,
      loading : false,
      user : user
    }
  }),
  on(userActions.updateAddressFailure, (state, {error}) => {
    return {
      ...state,
      loading : false
    }
  })
);

