import { createAction, props } from '@ngrx/store';
import {IProduct} from "../models/IProduct";

export const loadProducts = createAction(
  '[Product] Load Products'
);

export const loadProductsSuccess = createAction(
  '[Product] Load Products Success',
  props<{ data: any }>()
);

export const loadProductsFailure = createAction(
  '[Product] Load Products Failure',
  props<{ error: any }>()
);

// Upload Products
export const uploadProducts = createAction(
  '[Product] upload Products',
  props<{product : IProduct}>()
);

export const uploadProductsSuccess = createAction(
  '[Product] upload Products Success',
  props<{ product: IProduct }>()
);

export const uploadProductsFailure = createAction(
  '[Product] upload Products Failure',
  props<{ error: any }>()
);

// Get Men's Collection
export const getMensCollection = createAction(
  '[Product] Get Mens Collections'
);

export const getMensCollectionSuccess = createAction(
  '[Product] get Mens Collection Success',
  props<{ products: IProduct[] }>()
);

export const getMensCollectionFailure = createAction(
  '[Product] get Mens Collection Failure',
  props<{ error: any }>()
);

// Get women's Collection
export const getWomensCollection = createAction(
  '[Product] Get Womens Collections'
);

export const getWomensCollectionSuccess = createAction(
  '[Product] get womens Collection Success',
  props<{ products: IProduct[] }>()
);

export const getWomensCollectionFailure = createAction(
  '[Product] get womens Collection Failure',
  props<{ error: any }>()
);

// Get kid's Collection
export const getKidsCollection = createAction(
  '[Product] Get kids Collections'
);

export const getKidsCollectionSuccess = createAction(
  '[Product] get kids Collection Success',
  props<{ products: IProduct[] }>()
);

export const getKidsCollectionFailure = createAction(
  '[Product] get kids Collection Failure',
  props<{ error: any }>()
);

// Get a Single Product
export const getProductDetails = createAction(
  '[Product] Get Product Details',
  props<{productId : string}>()
);

export const getProductDetailsSuccess = createAction(
  '[Product] get Product Details Success',
  props<{ product: IProduct }>()
);

export const getProductDetailsFailure = createAction(
  '[Product] get Product Details Failure',
  props<{ error: any }>()
);

