import { createAction, props } from '@ngrx/store';
import {IProduct} from "../../products/models/IProduct";
import {IOrder} from "../models/IOrder";

export const loadOrders = createAction(
  '[Order] Load Orders'
);

export const loadOrdersSuccess = createAction(
  '[Order] Load Orders Success',
  props<{ orders: IOrder[] }>()
);

export const loadOrdersFailure = createAction(
  '[Order] Load Orders Failure',
  props<{ error: any }>()
);

// add To Cart
export const addToCart = createAction(
  '[Order] add To Cart',
  props<{product:IProduct , qty : number}>()
);

// incr Product Qty
export const incrProductQty = createAction(
  '[Order] Incr Product Qty',
  props<{product : IProduct}>()
);

// decr Product Qty
export const decrProductQty = createAction(
  '[Order] Decr Product Qty',
  props<{product : IProduct}>()
);

// Delete a cart Item
export const deleteCartItem = createAction(
  '[Order] Delete Cart Item',
  props<{product : IProduct}>()
);

// place an order
export const placeOrder = createAction(
  '[Order] Place Orders',
  props<{items : IProduct[] , tax : number , total : number}>()
);

export const placeOrderSuccess = createAction(
  '[Order] Place Order Success',
  props<{ order: IOrder }>()
);

export const placeOrderFailure = createAction(
  '[Order] Place Order Failure',
  props<{ error: any }>()
);

// send Cart Info to server
export const sendCartToServer = createAction(
  '[Order] sendCartToServer',
  props<{cartItems : IProduct[]}>()
);

export const sendCartToServerSuccess = createAction(
  '[Order] sendCartToServer Success',
  props<{ data: any }>()
);

export const sendCartToServerFailure = createAction(
  '[Order] sendCartToServer Failure',
  props<{ error: any }>()
);
