import { Action, createReducer, on } from '@ngrx/store';
import {IProduct} from "../models/IProduct";
import * as productActions from '../actions/product.actions';

export const productFeatureKey = 'product';

export interface State {
  loading : boolean,
  products : IProduct[],
  selectedProduct : IProduct,
  errorMessage : string
}

export const initialState: State = {
  loading : false,
  products : [],
  selectedProduct : null,
  errorMessage : ''
};

export const reducer = createReducer(
  initialState,
  on(productActions.uploadProducts, (state , {product}) => {
    return {
      ...state,
      loading : true
    };
  }),
  on(productActions.uploadProductsSuccess, (state , {product}) => {
    return {
      ...state,
      loading : false
    }
  }),
  on(productActions.uploadProductsFailure, (state , {error}) => {
    return {
      ...state,
      loading : false,
      errorMessage : error
    }
  }),
  // Get Men's Collection
  on(productActions.getMensCollection, (state) => {
    return {
      ...state,
      loading : true
    };
  }),
  on(productActions.getMensCollectionSuccess, (state , {products}) => {
    return {
      ...state,
      loading : false,
      products : products
    }
  }),
  on(productActions.getMensCollectionFailure, (state , {error}) => {
    return {
      ...state,
      loading : false,
      errorMessage : error
    }
  }),
  // Get Women's Collection
  on(productActions.getWomensCollection, (state) => {
    return {
      ...state,
      loading : true
    };
  }),
  on(productActions.getWomensCollectionSuccess, (state , {products}) => {
    return {
      ...state,
      loading : false,
      products : products
    }
  }),
  on(productActions.getWomensCollectionFailure, (state , {error}) => {
    return {
      ...state,
      loading : false,
      errorMessage : error
    }
  }),
  // Get Kid's Collection
  on(productActions.getKidsCollection, (state) => {
    return {
      ...state,
      loading : true
    };
  }),
  on(productActions.getKidsCollectionSuccess, (state , {products}) => {
    return {
      ...state,
      loading : false,
      products : products
    }
  }),
  on(productActions.getKidsCollectionFailure, (state , {error}) => {
    return {
      ...state,
      loading : false,
      errorMessage : error
    }
  }),
  // Get Product Details
  on(productActions.getProductDetails, (state) => {
    return {
      ...state,
      loading : true
    };
  }),
  on(productActions.getProductDetailsSuccess, (state , {product}) => {
    return {
      ...state,
      loading : false,
      selectedProduct : product
    }
  }),
  on(productActions.getProductDetailsFailure, (state , {error}) => {
    return {
      ...state,
      loading : false,
      errorMessage : error
    }
  })
);

