import { Injectable } from '@angular/core';
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import { environment } from "../../../environments/environment";
import {IUser} from "../models/IUser";
import {catchError, retry} from "rxjs/operators";
import {Observable, throwError} from "rxjs";

@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor(private httpClient:HttpClient) { }

  // registerUser
  public registerUser(user:IUser):Observable<{result : string}>{
    let dataURL = `${environment.apiURL}/user/register`;
    return this.httpClient.post<{result : string}>(dataURL, user).pipe(
      retry(1),
      catchError(this.handleError)
    );
  }

  // loginUser
  public loginUser(user:IUser):Observable<{result : string , token : string}>{
    let dataURL = `${environment.apiURL}/user/login`;
    return this.httpClient.post<{result : string , token : string}>(dataURL, user).pipe(
      retry(1),
      catchError(this.handleError)
    );
  }

  // loadUser
  public loadUser():Observable<IUser>{
    let dataURL = `${environment.apiURL}/user/`;
    return this.httpClient.get<IUser>(dataURL).pipe(
      retry(1),
      catchError(this.handleError)
    );
  }

  // update Address
  public updateAddress(address):Observable<IUser>{
    let dataURL = `${environment.apiURL}/user/address`;
    return this.httpClient.post<IUser>(dataURL, address).pipe(
      retry(1),
      catchError(this.handleError)
    );
  }

  public handleError(error:HttpErrorResponse){
    let errorMessage:string = "";

    if(error.error instanceof ErrorEvent){
      // client side error
      errorMessage = error.error.message;
    }
    else{
      // server error
      errorMessage = `Status : ${error.status}  MESSAGE : ${error.message}`;
    }
    return throwError(errorMessage);
  }
}
