import { Injectable } from '@angular/core';
import {Actions, createEffect, Effect, ofType} from '@ngrx/effects';
import {ProductService} from "../services/product.service";
import * as productActions from '../actions/product.actions';
import {catchError, concatMap, map, mergeMap, tap} from "rxjs/operators";
import {of} from "rxjs";
import {Router} from "@angular/router";

@Injectable()
export class ProductEffects {
  constructor(private actions$: Actions,
              private productService:ProductService,
              private router:Router) {}


  @Effect() // create a product
  uploadProduct = () => {
    return this.actions$.pipe(
      ofType(productActions.uploadProducts),
      concatMap((action) => this.productService.uploadProduct(action.product).pipe(
        map((product) => productActions.uploadProductsSuccess({product : product})),
        catchError((error) => of(productActions.uploadProductsFailure({error : error})))
      ))
    )
  };

  @Effect() // get Men's collection
  getMensCollection = () => {
    return this.actions$.pipe(
      ofType(productActions.getMensCollection),
      mergeMap((action) => this.productService.getMensCollection().pipe(
        map((products) => productActions.getMensCollectionSuccess({products : products})),
        catchError((error) => of(productActions.getMensCollectionFailure({error : error})))
      ))
    )
  };

  @Effect() // get Women's collection
  getWomensCollection = () => {
    return this.actions$.pipe(
      ofType(productActions.getWomensCollection),
      mergeMap((action) => this.productService.getWomensCollection().pipe(
        map((products) => productActions.getWomensCollectionSuccess({products : products})),
        catchError((error) => of(productActions.getWomensCollectionFailure({error : error})))
      ))
    )
  };

  @Effect() // get kid's collection
  getKidsCollection = () => {
    return this.actions$.pipe(
      ofType(productActions.getKidsCollection),
      mergeMap((action) => this.productService.getKidsCollection().pipe(
        map((products) => productActions.getKidsCollectionSuccess({products : products})),
        catchError((error) => of(productActions.getKidsCollectionFailure({error : error})))
      ))
    )
  };

  @Effect() // get Product Details
  getProductDetails = () => {
    return this.actions$.pipe(
      ofType(productActions.getProductDetails),
      mergeMap((action) => this.productService.getProductDetails(action.productId).pipe(
        map((product) => productActions.getProductDetailsSuccess({product : product})),
        catchError((error) => of(productActions.getProductDetailsFailure({error : error})))
      ))
    )
  };

  // Upload Product Success redirect to 'home page'
  @Effect({ dispatch: false })
  uploadProductsSuccess = this.actions$.pipe(
    ofType(productActions.uploadProductsSuccess),
    tap(() => this.router.navigate(['/']))
  );
}
