import { Injectable } from '@angular/core';
import {Actions, createEffect, Effect, ofType} from '@ngrx/effects';
import * as orderActions from '../actions/order.actions';
import {OrderService} from "../services/order.service";
import * as productActions from "../../products/actions/product.actions";
import {catchError, concatMap, map, mergeMap, tap} from "rxjs/operators";
import {of} from "rxjs";
import {Router} from "@angular/router";

@Injectable()
export class OrderEffects {

  constructor(private actions$: Actions,
              private orderService : OrderService,
              private router : Router) {}

  @Effect() // send cartItems to server
  sendCartToServer = () => {
    return this.actions$.pipe(
      ofType(orderActions.sendCartToServer),
      concatMap((action) => this.orderService.sendCartToServer(action.cartItems).pipe(
        map((msg) => orderActions.sendCartToServerSuccess({data : msg})),
        catchError((error) => of(orderActions.sendCartToServerFailure({error : error})))
      ))
    )
  };

  @Effect() // Place An Order
  placeOrder = () => {
    return this.actions$.pipe(
      ofType(orderActions.placeOrder),
      concatMap((action) => this.orderService.placeOrder({items : action.items , tax : action.tax , total : action.total}).pipe(
        map((order) => orderActions.placeOrderSuccess({order : order})),
        catchError((error) => of(orderActions.placeOrderFailure({error : error})))
      ))
    )
  };

  @Effect() // load all Orders
  loadOrders = () => {
    return this.actions$.pipe(
      ofType(orderActions.loadOrders),
      mergeMap((action) => this.orderService.loadOrders().pipe(
        map((orders) => orderActions.loadOrdersSuccess({orders : orders})),
        catchError((error) => of(orderActions.loadOrdersFailure({error : error})))
      ))
    )
  };

  // send cartItems to server Success redirect to 'checkout page'
  @Effect({ dispatch: false })
  sendCartToServerSuccess = this.actions$.pipe(
    ofType(orderActions.sendCartToServerSuccess),
    tap(() => this.router.navigate(['/orders/checkout']))
  );
}
