import { Action, createReducer, on } from '@ngrx/store';
import {IProduct} from "../../products/models/IProduct";
import * as orderActions from '../actions/order.actions';
import {IOrder} from "../models/IOrder";

export const orderFeatureKey = 'order';

export interface State {
  loading : boolean,
  cartItems : IProduct[],
  errorMessage : string,
  orders : IOrder[]
}

export const initialState: State = {
  loading : false,
  cartItems : [],
  errorMessage : '',
  orders : []
};

export const reducer = createReducer(
  initialState,
  on(orderActions.addToCart, (state , {product , qty}) => {
    let selectedProduct = {...product, qty : qty};
    let existingProduct = state.cartItems.find(cartItem => cartItem._id == product._id);
    if(!existingProduct){
      return {
        ...state,
        cartItems : [...state.cartItems , selectedProduct]
      }
    }
    else{
      return state;
    }
  }),
  on(orderActions.incrProductQty, (state , {product}) => {
    let updatedCartItems = state.cartItems.map(cartItem => {
      if(cartItem._id === product._id){
        return {
          ...cartItem,
          qty : cartItem.qty + 1
        }
      }
      return  cartItem;
    });
    return {
      ...state,
      cartItems : [...updatedCartItems]
    }
  }),
  on(orderActions.decrProductQty, (state , {product}) => {
    let updatedCartItems = state.cartItems.map(cartItem => {
      if(cartItem._id === product._id){
        return {
          ...cartItem,
          qty : (cartItem.qty - 1 > 0) ? cartItem.qty - 1 : 1
        }
      }
      return  cartItem;
    });
    return {
      ...state,
      cartItems : [...updatedCartItems]
    }
  }),
  on(orderActions.deleteCartItem, (state , {product}) => {
    let updatedCartItems = state.cartItems.filter(cartItem => cartItem._id !== product._id);
    return {
      ...state,
      cartItems : [...updatedCartItems]
    }
  }),
  // send cart Items to server
  on(orderActions.sendCartToServer, (state , {cartItems}) => {
    localStorage.setItem('cartItems', JSON.stringify(cartItems));
    return {
      ...state,
      loading : true
    }
  }),
  on(orderActions.sendCartToServerSuccess, (state , {data}) => {
    return {
      ...state,
      loading : false
    }
  }),
  on(orderActions.sendCartToServerFailure, (state , {error}) => {
    return {
      ...state,
      loading : false,
      errorMessage : error
    }
  }),
  // Place An Order
  on(orderActions.placeOrder, (state , {items , tax , total}) => {
    return {
      ...state,
      loading : true
    }
  }),
    on(orderActions.placeOrderSuccess, (state , {order}) => {
      return {
        ...state,
        loading : false
      }
    }),
    on(orderActions.placeOrderFailure, (state , {error}) => {
      return {
        ...state,
        loading : false,
        errorMessage : error
      }
    }),
  // Load All Orders
  on(orderActions.loadOrders, (state) => {
    return {
      ...state,
      loading : true
    }
  }),
  on(orderActions.loadOrdersSuccess, (state , {orders}) => {
    return {
      ...state,
      loading : false,
      orders : orders
    }
  }),
  on(orderActions.loadOrdersFailure, (state , {error}) => {
    return {
      ...state,
      loading : false,
      orders : [],
      errorMessage : error
    }
  })
);

