import { Injectable } from '@angular/core';
import {Actions, createEffect, Effect, ofType} from '@ngrx/effects';
import * as userActions from '../actions/user.actions';
import * as userReducer from '../reducers/user.reducer';
import {UserService} from "../services/user.service";
import {Router} from "@angular/router";
import * as productActions from "../../products/actions/product.actions";
import {catchError, concatMap, map, mergeMap, tap} from "rxjs/operators";
import {of} from "rxjs";


@Injectable()
export class UserEffects {
  constructor(private actions$: Actions,
              private userService:UserService,
              private router:Router) {}

  @Effect() // registerUser
  registerUser = () => {
    return this.actions$.pipe(
      ofType(userActions.registerUser),
      concatMap((action) => this.userService.registerUser(action.user).pipe(
        map((obj) => userActions.registerUserSuccess({result : obj.result})),
        catchError((error) => of(userActions.registerUserFailure({error : error})))
      ))
    )
  };

  @Effect() // loginUser
  loginUser = () => {
    return this.actions$.pipe(
      ofType(userActions.loginUser),
      concatMap((action) => this.userService.loginUser(action.user).pipe(
        map((obj) => userActions.loginUserSuccess({result : obj.result , token : obj.token})),
        catchError((error) => of(userActions.loginUserFailure({error : error})))
      ))
    )
  };

  @Effect() // loadUser
  loadUser = () => {
    return this.actions$.pipe(
      ofType(userActions.loadUser),
      mergeMap((action) => this.userService.loadUser().pipe(
        map((user) => userActions.loadUserSuccess({user : user})),
        catchError((error) => of(userActions.loadUserFailure({error : error})))
      ))
    )
  };

  @Effect() // update Address
  updateAddress = () => {
    return this.actions$.pipe(
      ofType(userActions.updateAddress),
      concatMap((action) => this.userService.updateAddress(action.address).pipe(
        map((user) => userActions.updateAddressSuccess({user : user})),
        catchError((error) => of(userActions.updateAddressFailure({error : error})))
      ))
    )
  };

  // register User Success redirect to 'login page'
  @Effect({ dispatch: false })
  registerUserSuccess = this.actions$.pipe(
    ofType(userActions.registerUserSuccess),
    tap(() => this.router.navigate(['/users/login']))
  );

  // login User Success redirect to 'home page'
  @Effect({ dispatch: false })
  loginUserSuccess = this.actions$.pipe(
    ofType(userActions.loginUserSuccess),
    tap(() => this.router.navigate(['/']))
  );

}
