import { Injectable } from '@angular/core';
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {Observable, throwError} from "rxjs";
import {environment} from "../../../environments/environment";
import {IProduct} from "../../products/models/IProduct";
import {catchError, retry} from "rxjs/operators";
import {IOrder} from "../models/IOrder";

@Injectable({
  providedIn: 'root'
})
export class OrderService {
    constructor(private httpClient:HttpClient) { }

    // send Cart Items to Server
    public sendCartToServer(cartItems):Observable<any>{
      let dataURL:string = `${environment.apiURL}/payment/send-cart`;
      return this.httpClient.post<any>(dataURL, cartItems).pipe(
        retry(1),
        catchError(this.handleError)
      )
    }

    // place Order
   public placeOrder({items , tax , total}):Observable<IOrder>{
     let dataURL:string = `${environment.apiURL}/order`;
     return this.httpClient.post<IOrder>(dataURL, {items , tax , total}).pipe(
       retry(1),
       catchError(this.handleError)
     )
   }

  // load all Orders
  public loadOrders():Observable<IOrder[]>{
    let dataURL:string = `${environment.apiURL}/order`;
    return this.httpClient.get<IOrder[]>(dataURL).pipe(
      retry(1),
      catchError(this.handleError)
    )
  }

  public handleError(error:HttpErrorResponse){
    let errorMessage:string = "";

    if(error.error instanceof ErrorEvent){
      // client side error
      errorMessage = error.error.message;
    }
    else{
      // server error
      errorMessage = `Status : ${error.status}  MESSAGE : ${error.message}`;
    }
    return throwError(errorMessage);
  }
}
