import { createAction, props } from '@ngrx/store';
import {IUser} from "../models/IUser";
import {IAddress} from "../models/IAddress";

// Get User Info from server , after login
export const loadUser = createAction(
  '[User] Load User'
);

export const loadUserSuccess = createAction(
  '[User] Load User Success',
  props<{ user: IUser }>()
);

export const loadUserFailure = createAction(
  '[User] Load User Failure',
  props<{ error: any }>()
);

// register a user
export const registerUser = createAction(
  '[User] register User',
  props<{user : IUser}>()
);

export const registerUserSuccess = createAction(
  '[User] register User Success',
  props<{ result: string }>()
);

export const registerUserFailure = createAction(
  '[User] register User Failure',
  props<{ error: any }>()
);

// Login a user
export const loginUser = createAction(
  '[User] Login User',
  props<{user : IUser}>()
);

export const loginUserSuccess = createAction(
  '[User] Login User Success',
  props<{ result: string , token : string }>()
);

export const loginUserFailure = createAction(
  '[User] Login User Failure',
  props<{ error: any }>()
);

// Logout  a user
export const logOut = createAction(
  '[User] Logout User'
);

// Update Address of user
export const updateAddress = createAction(
  '[User] Update Address',
  props<{address : IAddress}>()
);

export const updateAddressSuccess = createAction(
  '[User] Update Address Success',
  props<{ user: IUser }>()
);

export const updateAddressFailure = createAction(
  '[User] Update Address Failure',
  props<{ error: any }>()
);


