import { Injectable } from '@angular/core';
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {IProduct} from "../models/IProduct";
import { environment } from "../../../environments/environment";
import {catchError, retry} from "rxjs/operators";
import {Observable, throwError} from "rxjs";

@Injectable({
  providedIn: 'root'
})
export class ProductService {
  constructor(private httpClient:HttpClient) { }

  // upload Product
  public uploadProduct(product:IProduct):Observable<IProduct>{
    let dataURL:string = `${environment.apiURL}/product/upload`;
    return this.httpClient.post<IProduct>(dataURL, product).pipe(
      retry(1),
      catchError(this.handleError)
    )
  }

  // get Men's Collection
  public getMensCollection():Observable<IProduct[]>{
    let dataURL:string = `${environment.apiURL}/product/men`;
    return this.httpClient.get<IProduct[]>(dataURL).pipe(
      retry(1),
      catchError(this.handleError)
    )
  }

  // get Women's Collection
  public getWomensCollection():Observable<IProduct[]>{
    let dataURL:string = `${environment.apiURL}/product/women`;
    return this.httpClient.get<IProduct[]>(dataURL).pipe(
      retry(1),
      catchError(this.handleError)
    )
  }

  // get kid's Collection
  public getKidsCollection():Observable<IProduct[]>{
    let dataURL:string = `${environment.apiURL}/product/kids`;
    return this.httpClient.get<IProduct[]>(dataURL).pipe(
      retry(1),
      catchError(this.handleError)
    )
  }

  // get Product Details
  public getProductDetails(productID):Observable<IProduct>{
    let dataURL:string = `${environment.apiURL}/product/${productID}`;
    return this.httpClient.get<IProduct>(dataURL).pipe(
      retry(1),
      catchError(this.handleError)
    )
  }

  public handleError(error:HttpErrorResponse){
    let errorMessage:string = "";

    if(error.error instanceof ErrorEvent){
      // client side error
      errorMessage = error.error.message;
    }
    else{
      // server error
      errorMessage = `Status : ${error.status}  MESSAGE : ${error.message}`;
    }
    return throwError(errorMessage);
  }
}
